.main-bar {
  width: calc(100% - 300px - 24px);
  float: left;
  margin: 25px 0px;
  padding: 0;
}
.main-bar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}
.main-bar-header h1 {
  font-weight: 400;
}
.main-bar-header .ask-btn {
  padding: 10px 15px;
  border-radius: 4px;
  background-color: #009dff;
  color: white;
  border: none;
  text-decoration: none;
  transition: 0.3s;
}
.main-bar-header .ask-btn:hover {
  background-color: #0086d8;
}
.display-question-container {
  min-height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fdf7e2;
  border-bottom: solid 1px #edeff0;
  margin-bottom: 5px;
}
.display-question-container .display-votes-ans {
  padding: 20px;
}
.display-question-container .display-votes-ans p {
  margin: 0%;
  text-align: center;
}
.display-question-details {
  flex-grow: 1;
  padding: 0px 20px;
}
.question-title-link {
  text-decoration: none;
  color: #037ecb;
  transition: 0.3s;
}
.question-title-link:hover {
  color: #009dff;
}
.display-question-details p {
  padding: 4px;
  margin: 0%;
}
.display-tags-time .display-tags {
  display: flex;
  flex-wrap: wrap;
}
.display-tags-time {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.display-tags-time .display-tags p {
  margin: 2px;
  padding: 4px;
  font-size: 13px;
  background-color: #edeff0;
  color: #39739d;
}
.display-tags-time .display-time {
  font-size: 13px;
}
@media (min-width: 320px) {
  .main-bar-header h1 {
    font-size: 12px;
  }
  .main-bar-header button {
    font-size: 10px;
  }
  .main-bar-header {
    margin-top: 25px;
  }
}
@media (min-width: 375px) {
  .main-bar-header h1,
  .main-bar-header button {
    font-size: 13px;
  }
}
@media (min-width: 576px) {
  .main-bar-header h1,
  .main-bar-header button {
    font-size: 20px;
  }
}
@media (max-width: 576px) {
  .display-votes-ans {
    display: none;
  }
}
@media (max-width: 768px) {
  .main-bar {
    width: 100%;
  }
  .main-bar-header {
    margin-top: 15px;
  }
}
@media (width: 1024px) {
  .main-bar {
    width: calc(100% - 275px);
  }
  .main-bar-header {
    margin-top: 13px;
  }
}
